@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body {
  background-color: #28abb9;
  font-family: "Montserrat", sans-serif;
  color: #2d6187;
}

.App {
  text-align: center;
}

.cart {
  background-color: #effad3;
  border-radius: 20px;
  width: 85%;
  margin: auto;
  padding: 2px 10px;
}

.cartTitle {
  padding-bottom: 4px;
  border-bottom: solid 4px #a8dda8;
}

.cartItem {
  display: grid;
  grid-template-columns: 3fr 2.5fr 1fr 1.5fr;
  border-bottom: solid 1px #a8dda8;
  justify-content: space-around;
}

.itemName {
  display: grid;
  grid-template-columns: 1fr 2fr;
}

.itemBtn {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  align-content: center;
  margin: 0 auto;
}

.itemBtn button {
  margin: 5px;
  width: 40px;
  height: 40px;
  border-radius: 10px;
  border: none;
  font-size: 1rem;
  background-color: #a8dda8;
  color: #2d6187;
}

.deleteBtn {
  display: grid;
  grid-template-columns: 1fr;
  align-content: center;
  margin: 0 auto;
}

.deleteBtn button {
  width: 80px;
  height: 40px;
  border-radius: 5px;
  border: none;
  font-weight: bold;
  font-size: 0.8rem;
  background-color: #a8dda8;
  color: #2d6187;
}

.itemPrice {
  align-content: center;
}

.sum {
  background-color: #a8dda8;
  border-radius: 10px;
  width: 50%;
  margin: 8px auto;
  padding: 1px;
}

@media only screen and (max-width: 700px) {
  .cartItem {
    padding: 0 4px;
    display: grid;
    grid-template-rows: 1fr 0.5fr 0.5fr 0.5fr;
    grid-template-columns: auto;
  }

  h3 {
    font-size: 1rem;
    margin: auto;
  }
  .itemPrice {
    margin-bottom: auto;
  }
  .itemBtn button {
    width: 30px;
    height: 30px;
  }

  .deleteBtn button {
    margin-bottom: 10px;
  }
}

